@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  overflow-x: hidden;
}

* {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}


.app {
  background-color: #111;
}


.row{margin-left:20px}.row>h2{color:#eee;font-size:1.4rem;text-transform:uppercase;font-weight:600}.row__posters{display:flex;overflow-y:hidden;overflow-x:auto;padding:20px}.row__posters::-webkit-scrollbar{display:none}.row__poster{object-fit:contain;width:100%;max-height:100px;transition:transform 400ms;margin-right:10px}.row__poster:hover{transform:scale(1.08)}.row__poster--large{max-height:250px}
.banner{width:100%;height:500px;background-size:cover;background-position:center 8%;color:#fff;position:relative}.banner_title{font-size:3rem;font-weight:800;padding-bottom:.3rem}.banner_contents{margin-left:30px;padding-top:140px;height:190px}.banner_description{max-width:45rem;line-height:1.3;padding-top:1rem;font-size:.8rem}.banner_button{cursor:pointer;color:#fff;outline:none;border:none;font-weight:700;border-radius:.2vw;padding-left:2rem;padding-right:2rem;margin-right:2rem;padding-top:.5rem;padding-bottom:.5rem;background-color:rgba(51,51,51,.5)}.banner_button:hover{color:#000;background-color:#e6e6e6;transition:all .2s}.banner--fadeBottom{height:7.4rem;background-image:linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);position:absolute;bottom:0;left:0;width:100%}
.nav{position:fixed;width:100%;display:flex;justify-content:space-between;align-items:center;z-index:1;padding:20px;height:30px;transition:all .5s ease-in}.nav_black{background-color:#111}.nav_logo{width:80px;position:fixed;left:20px}.nav_avatar{width:30px;object-fit:contain;position:fixed;right:20px}
